body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', Courier, monospace;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: $dark-black;
    background-color: $white;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}